/***
*   
*  
* inputchange
*  @param {?Number|300} time - 
*  @param {Function} fn - 
*  @param {?String|"click"} event -  "click"
*  @param {Array} binding.value - [fn,event,time]
*  @date  2020-1-31
*  @author aChuan
*  <el-button v-throttle="[reset,`click`,300]"></el-button>
*  <el-button v-throttle="[()=>reset(param),`click`,300]"></el-button>
*/

export default {
  inserted (el, binding) {
    const [fn, event = 'click', time = 300] = binding.value
    let timer, timerEnd
    el.addEventListener(event, () => {
      if (timer) {
        clearTimeout(timerEnd)
        // eslint-disable-next-line no-return-assign
        return timerEnd = setTimeout(() => fn(), time)
      }
      fn()
      // eslint-disable-next-line no-return-assign
      timer = setTimeout(() => timer = null, time)
    })
  }
}
